import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import styled from "styled-components"

const Wrapper = styled.div`
    font-size: 5rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient(to right top, white, rgb(211, 216, 219));
`;

const Alldiv = styled.div`
    width: 100%;
`;

const Userdiv = styled.div`
    font-size: 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    margin-top: 2rem;
    background-image: linear-gradient(to right top, rgb(129, 136, 140), rgb(93, 107, 117));
    @media (min-width: 726px) {
        margin-left: 25%;
        margin-right: 25%;
    }
    :hover {
        box-shadow: 0 5px 10px black, 0 15px 40px gray;
    }
`;

const Styledlink = styled(Link)`
    text-decoration: none;
    padding: 3rem;
    color: black;
    font-weight: bolder;
    :hover {
        text-shadow: 0 5px 10px black, 0 15px 40px gray;
    }
`;

const Formlink = styled(Link)`
    margin: 1rem;
    padding: 1rem;
    text-decoration: none;
    color: white;
    background-color: cornflowerblue;
`;

const Buttondivs = styled.div`
    display: flex;
    justify-content: center;
    
`;

const Buttonnon = styled.div`
    padding: 1rem;
    color: black;
    background: lightgreen;
    margin: 2rem;
    border: none;
    border-radius: 1rem;
    :hover {
        background: green;
    }
`;

const Button = styled(Link)`
    text-decoration: none;
    margin: 2rem;
    border: none;
    border-radius: 1rem;
    padding: 1rem;
    color: black;
    background: lightgreen;
    :hover {
        background: green;
    }
`;

const Input = styled.input`
    width: 40%;
`;

const Userlist = () => {

    const [ users, setUsers ] = useState([])
    const [ filtered, setFiltered ] = useState([])

    useEffect(() => {
        fetch("/get_users")
        .then(res => res.json())
        .then(data => {
            setUsers(data)
            setFiltered(data)
        })
        .catch(err => console.error(err))
    }, [])

    function deleteUser(e) {
        const id = e.target.getAttribute("name")
        
        fetch("/delete_user", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({id})
        })

        users.forEach((e, index) => {
            if (e.id == id) {
                users.splice(index, 1)
                setUsers([...users])
                setFiltered([...users])
            }
        });
    }

    function Searchbar(e) {
        const search = users.filter(elem => {
            const string = elem.ime + " " + elem.prezime
            return string.toLowerCase().includes(e.target.value) || string.toUpperCase().includes(e.target.value)
        })
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Formlink to="korisnik_obrazac">Dodaj člana +</Formlink>
            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />
            <Alldiv>
            {filtered && filtered.length ? filtered.map(user => {
                return (
                    <Userdiv key={user.id}>
                        <Styledlink 
                            to={{
                                pathname: `korisnik/${user.id}`,
                                state: user.id 
                        }}>
                            {user.ime} {user.prezime}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{
                                    pathname: `korisnik_obrazac/${user.id}`,
                                    state: user.id 
                            }}>
                                Ažuriraj
                            </Button>
                            <Buttonnon onClick={deleteUser} name={user.id}>Izbriši</Buttonnon>
                        </Buttondivs>
                    </Userdiv>
                )
            })
            :
            <Userdiv>
                Nema članova za prikazati...
            </Userdiv>
        }
        </Alldiv>
        </Wrapper>
    )
};

export default Userlist;
