import { Link, useParams } from "react-router-dom"
import { useState } from "react"
import styled from "styled-components"

const Wrapper = styled.div`
    font-size: 5rem;
    display: flex;
    flex-direction: column;
`;

const TimDetalji = () => {

    const { id } = useParams()

    return (
        <Wrapper>
            {id}
        </Wrapper>
    )
}

export default TimDetalji;
