import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import styled from "styled-components"

const Wrapper = styled.div`
    font-size: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const Alldiv = styled.div`
    width: 100%;
`;

const Eventdiv = styled.div`
    font-size: 4rem;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
    margin: 2.5rem;
    border-radius: 2rem;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(214, 210, 208));
    @media (min-width: 726px) {
        margin-left: 25%;
        margin-right: 25%;
    }
`;

const Styledlink = styled(Link)`
    text-decoration: none;
`;

const Buttondivs = styled.div`

`;

const Buttonnon = styled.button`
    margin: 1rem;
    border: none;
    border-radius: 1rem;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(214, 210, 208));
`;

const Button = styled(Link)`
    margin: 1rem;
    border: none;
    border-radius: 1rem;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(214, 210, 208));
`;

const Input = styled.input`
    width: 40%;
`;

const Eventlist = () => {

    const [ events, setEvents ] = useState([])
    const [ filtered, setFiltered ] = useState([])

    useEffect(() => {
        fetch("/get_events")
        .then(res => res.json())
        .then(data => {
            setEvents(data)
            setFiltered(data)
        })
        .catch(err => console.error(err))
    }, [])

    function deleteEvent(e) {
        const id = e.target.name
        fetch("/delete_event", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({id})
        })
        events.forEach((e, index) => {
            if (e.id == id) {
                events.splice(index, 1)
                setEvents([...events])
                setFiltered([...events])
            }
        });
    }

    function Searchbar(e) {
        const search = events.filter(elem => {
            const string = elem.ime
            return string.toLowerCase().includes(e.target.value) || string.toUpperCase().includes(e.target.value)
        })
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Styledlink to="event_obrazac">Dodaj event +</Styledlink>
            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />
            <Alldiv>
            {filtered && filtered.length ? filtered.map(event => {
                return (
                    <Eventdiv key={event.id}>
                        <Styledlink 
                            to={{
                                pathname: `event/${event.id}`,
                                state: event.id 
                        }}>
                            {event.ime}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{
                                    pathname: `event_obrazac/${event.id}`,
                                    state: event.id 
                            }}>
                                Ažuriraj
                            </Button>
                            <Buttonnon onClick={deleteEvent} name={event.id} >Izbriši</Buttonnon>
                        </Buttondivs>
                    </Eventdiv>
                )
            })
            :
            <Eventdiv>
                Nema eventa za prikazati...
            </Eventdiv>
        }
        </Alldiv>
        </Wrapper>
    )
};

export default Eventlist;
