import { Link } from "react-router-dom"
import styled from "styled-components"

const Navstyle = styled.nav`
    display: flex;
    justify-content: flex-end;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(213, 247, 173));
`;

const Styledlink = styled(Link)`
    margin: 2rem;
    padding-left: 3rem;
    padding-right: 3rem;
    font-size: 3rem;
    text-decoration: none;
    border-radius: 4rem;
    background-image: linear-gradient(to right top, rgb(181, 245, 144), rgb(142, 245, 207));; 
    @media (max-width: 726px) {
        display: none;
    }
    :hover {
        background-image: linear-gradient(to right top, rgba(181, 245, 144, 0.5), rgba(142, 245, 207, 0.5));; 
    }
`;

const Burgericon = styled.div`
    padding: 1.8rem;
    @media (min-width: 726px) {
        display: none;
    }
`;

const Burgerbar = styled.div`  
    height: 0.5rem;
    width: 3.5rem;
    background-color: black;
    margin: 6px 0;
`;

const Navbar = () => {

    function Logout() {
        fetch("/logout")
        localStorage.clear()
        window.location.href="/"
    }

    return (
        <Navstyle>
            <Styledlink to="/">Članovi</Styledlink>
            <Styledlink to="/timovi">Timovi</Styledlink>
            <Styledlink to="/eventi">Eventi</Styledlink>
            <Styledlink to="" onClick={Logout}>Odjava</Styledlink>
            <Burgericon>
                <Burgerbar></Burgerbar>
                <Burgerbar></Burgerbar>
                <Burgerbar></Burgerbar>
            </Burgericon>
        </Navstyle>
    )
}

export default Navbar;
